/* Roboto, Rubik Wet Paint */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&family=Rubik+Wet+Paint&display=swap');
/* Lato */
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400&display=swap');
/* Noto Sans */
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap');

@font-face {
  font-family: "pbio";
  src: url(./fonts/pbio-bold\ OTF.otf);
}

@font-face {
  font-family: "pretendard";
  src: url(./fonts/Pretendard-Regular.otf);
}

@font-face {
  font-family: "esamanru";
  src: url(./fonts/esamanru_Bold.otf);
}

@font-face {
  font-family: "esamanruM";
  src: url(./fonts/esamanru_Medium.otf);
}

.App {
  width: 100%;
  background-color: black;
}


/* Navigation Bar */
.nav-bar-div {
  position: sticky;
  top: 0;
  background-color: black;
  justify-content: center;
  display: flex;
  align-items: center;
  height: 5rem;
  z-index: 1;
  /* font-family: 'pbio'; */
  font-family: 'Lato', sans-serif;
}

.nav-bar-link {
  color: white;
  margin: 0 0.7rem;
  cursor: pointer;
  font-size: 1rem;
}


/* Main */

.main-div {
  position: relative;
  text-align: center;
}

.main1 {
  width: 100%;
  /* position: absolute; */
  bottom: 0rem;
  top: 0rem;
  left: 0rem;
}
.story-img {
    /* width: 100%; */
    /* height: 15rem; */
    width: 100%;
    display: block;
}